import React, {useState, useEffect} from 'react';
import logoImg from '../img/logo.jpg';
import headerImg from '../img/header.png';
import Post from './Post.jsx';

function FacebookClone() {
    const [posts, setPosts] = useState([]);
    
    useEffect(()=>
        {
            fetch("https://api.jsonbin.io/b/5f0730a9343d624b07828c64")
            .then( res => res.json() )
            .then( data => setPosts(data.posts) );
        }, []
    );

    return (
        <>
            <div>
                {/* nav */}
                <nav className="navbar navbar-default">
                    <div className="container-fluid">
                        <div className="navbar-header">
                            <a className="navbar-brand" href="index.html">Facebook</a>
                        </div>
                        <ul className="nav navbar-nav navbar-right">
                            <li><a href="home.html">Home</a></li>
                            <li><a href="profile.html">Profile</a></li>
                            <li><a href="#">Logout</a></li>
                        </ul>
                    </div>
                </nav>
                {/* ./nav */}
                {/* main */}
                <main className="container">
                    <div className="row">
                        {/* 1st Column */}
                        <div className="col-md-3">
                            {/* profile brief */}
                            <div className="panel panel-default">
                                <div className="panel-body">
                                    <img src={logoImg} alt="logo" width="100%" style={{ borderRadius: '50%' }} />
                                    <h4>PeopleCert</h4>
                                    <p>@peoplecert.org</p>
                                </div>
                            </div>
                            {/* ./profile brief */}
                            {/* edit profile */}
                            <div className="panel panel-default">
                                <div className="panel-body">
                                    <h4>Edit profile</h4>
                                    <form method="post">
                                        <div className="form-group">
                                            <input className="form-control" type="text" name="status" placeholder="Status" defaultValue />
                                        </div>
                                        <div className="form-group">
                                            <input className="form-control" type="text" name="location" placeholder="Location" defaultValue />
                                        </div>
                                        <div className="form-group">
                                            <input className="btn btn-primary" type="submit" name="update_profile" defaultValue="Save" />
                                        </div>
                                    </form>
                                </div>
                            </div>
                            {/* ./edit profile */}
                        </div>
                        {/* Middle Column */}
                        <div className="col-md-6">
                            {/* user profile */}
                            <div className="panel panel-default">
                                <img src={headerImg} alt="webpage header" className="media-object" style={{ width: '100%' }} />
                                <div className="panel-body">
                                    <button className="btn btn-default">Like</button>
                                    <button className="btn btn-default">Share</button>
                                </div>
                            </div>
                            <hr />
                            {/* user profile */}
                            {/* post form */}
                            <form method="post">
                                <div className="input-group">
                                    <input className="form-control" type="text" name="content" placeholder="Make a post..." />
                                    <span className="input-group-btn">
                                        <button className="btn btn-success" type="submit" name="post">Post</button>
                                    </span>
                                </div>
                            </form><hr />
                            {/* ./post form */}
                            {/* feed */}
                            <div>
                                {/* Loop and display posts here! */}
                                {posts.map(
                                    (post) => {
                                        return <Post key={post.id} post={post}/>
                                    }
                                )}
                            </div>
                            {/* ./feed */}
                        </div>
                        {/* 3rd Column */}
                        <div className="col-md-3">
                            {/* friends */}
                            <div className="panel panel-default">
                                <div className="panel-body">
                                    <h4>Friends</h4>
                                    <ul>
                                        <li>
                                            <a href="#">Harvard University</a>
                                            <a className="text-danger" href="#">[unfriend]</a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            {/* ./friends */}
                        </div>
                    </div>
                </main>
                {/* ./main */}
                {/* footer */}
                <footer className="container text-center">
                </footer>
                {/* ./footer */}
            </div>
        </>
    );
}

export default FacebookClone;