import React from 'react';
import { useState } from 'react';

function Post(props) {
    const {date, content, tags, img} = props.post;
    const [likes, setLikes] = useState(props.post.likes);
    const [isLikePressed, setIsLikePressed] = useState(false);
    const [shares, setShares] = useState(props.post.shares);
    const [isSharePressed, setIsSharePressed] = useState(false);

    function handleLikes(){
        if(!isLikePressed){
            setLikes(likes + 1);
            setIsLikePressed(true);
        } else{
            setLikes(likes - 1);
            setIsLikePressed(false);
        }
    }

    function handleShares(){
        if(!isSharePressed){
            setShares(shares + 1);
            setIsSharePressed(true);
        } else{
            setShares(shares - 1);
            setIsSharePressed(false);
        }
    }

    return (
        <div className="panel panel-default">
            <div className="panel-body">
                <p>Date: {date}</p>
            </div>
            <div className="panel-body">
                {content.map(
                    (para, ind) => {
                        return <p key={ "para" + ind }> {para} </p>
                    }
                )}
            </div>
            <div className="panel-body">
                <img src={img} alt="post promotion" stlyle={{width: "100%"}}/>
            </div>
            {(tags.length > 0) ? tags.map(
                (tag, ind) => {
                    return(
                        <div key={ "tag" + ind } className="panel-body">
                            <a className="bg-primary tag-link"> #{tag} </a>
                        </div>
                    );
                }
            ):<></>}
            <div className="panel-body">
                <p>
                    <span>Likes: {likes}</span>
                    <span className="pull-right">Shares: {shares}</span>
                </p>
            </div>
            <div className="panel-footer">
                <button onClick={handleLikes} className="btn btn-default">
                    {isLikePressed ? "Unlike" : "Like"}
                </button>
                <button onClick={handleShares} className="pull-right btn btn-default">
                    {isSharePressed ? "Unshare" : "Share"}
                </button>
            </div>
        </div>
    );
}

export default Post;